import React, { Component } from 'react';

class Status extends Component {
    render() {
        return (
            <div>
                <img src="data:image/png;base64,"
                style={{height: '50px', width: '50px', marginLeft: '400px'}}/>
            </div>
        )
    }
}

export default Status;


